<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + practitionerId)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canRenewLicense">
          <v-btn
            @click="$router.push('/custom/nmcb-license-renewal?practitioner=' + practitionerId + '&path=/components/nmcb')"
            class="primary"
            dark
          >
            <v-icon light left>mdi-autorenew</v-icon>
            <span>Apply License Renewal</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>
        <v-list-item v-if="$store.state.user.role === 'nmcb-role-records-officer' && registration.resource.id">
          <v-btn
          @click="$router.push('/questionnaire/nmcb-registration/nmcb-registration/' + registration.resource.id + '?practitioner=' + practitionerId)"
            class="primary"
            dark
          >
            <v-icon light left>mdi-autorenew</v-icon>
            <span>Edit Registration</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddRenewalFee">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-renewal-fee/nmcb-renewal-fee?practitioner=' + practitionerId + '&application=' + applicationId)"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Add Renewal Fee</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddRenewalFeeReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-renewal-fee-review/nmcb-renewal-fee-review-status?practitioner=' + practitionerId + '&application=' + applicationId)"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="renewalFeeReview.status && renewalFeeReview.status.code !=='pending'">Change Status</span>
            <span v-else>Accept/Decline Renewal Fee</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canAddRegistrarReview">
          <v-btn
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/nmcb-registrar-renewal-review-status/nmcb-registrar-renewal-review-status?practitioner=' + practitionerId + '&application=' + applicationId)"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span v-if="registrarReview.status.code !=='pending'">Change Renewal Status</span>
            <span v-else>Renewal Approval</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canPrintCertificate">
          <v-btn
            @click="print_certificate('initial_certificate')"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Initial Certificate</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canPrintCertificate">
          <v-btn
            @click="print_certificate('practising_certificate')"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Practising Certificate</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canPrintCertificate">
          <v-btn
            @click="print_certificate('provisional_certificate')"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Provisional Certificate</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="renewDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          License Renewal
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="renewDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-menu
            v-model="dateMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="renewalDateFormatted"
                label="Renewal Date*"
                prepend-icon="mdi-event"
                readonly
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="renewalDate"
              @change="renewalDateFormatted = formatDate(renewalDate)"
              @input="dateMenu = false"
            />
          </v-menu>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" :disabled="!renewalDate" @click="renew">Renew</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-container class="my-3" v-if="license.resource.id">
      <v-card
        class="mx-auto"
        max-width="700"
        outlined
      >
        <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
          License Information
        </v-card-title>
        <v-card-text class="my-3">
          <v-alert
            dense
            outlined
            type="error"
            v-if="license.status === 'Expired'"
          >
            {{ license.status }}
          </v-alert>
          <v-alert
            dense
            outlined
            type="success"
            v-else-if="license.status === 'Active'"
          >
            {{ license.status }}
          </v-alert>
          <v-alert
            dense
            outlined
            type="success"
            v-else
          >
            {{ license.status }}
          </v-alert>
          <v-row>
            <v-col>
              <v-chip class="ma-2" color="secondary" v-if="license.start !== '-'">Start: {{ $moment(license.start).format("DD-MM-YYYY") }}</v-chip>
              <v-chip class="ma-2" color="secondary" v-else>Start: {{ license.start }}</v-chip>
            </v-col>
            <v-spacer></v-spacer>
            <v-col>
              <v-chip class="ma-2" color="secondary" v-if="license.end !== '-'">End: {{ $moment(license.end).format("DD-MM-YYYY") }}</v-chip>
              <v-chip class="ma-2" color="secondary" v-else>End: {{ license.end }}</v-chip>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-container>
    <bw-page-view :id="queries.id" page="nmcb-registration" :key="regCompKey" />
    <v-container class="my-3" v-if="renewalApplication.status">
      <v-card
        class="mx-auto"
        max-width="700"
        outlined
      >
        <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
          Renewal Application
        </v-card-title>
        <v-card-text class="my-3">
          <v-row class="justify-space-between">
            <v-col cols="4">
              <span class="font-weight-bold">Registration Number(s):</span>
            </v-col>
            <v-col cols="8">{{ renewalApplication.regNumbs.join(", ") }}</v-col>
          </v-row>
          <v-divider/>
          <v-row class="justify-space-between">
            <v-col cols="4">
              <span class="font-weight-bold"><b>Status:</b></span>
            </v-col>
            <v-col cols="8"><b>{{ renewalApplication.status.display }}</b></v-col>
          </v-row>
          <v-divider/>
          <v-row class="justify-space-between">
            <v-col cols="4">
              <span class="font-weight-bold">Application Date:</span>
            </v-col>
            <v-col cols="8">{{ renewalApplication.date }}</v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-container>
    <bw-page-view v-if="renewalFee.id" :id="renewalFee.id" page="nmcb-renewal-fee" />
    <bw-page-view v-if="renewalFeeReview.id" :id="renewalFeeReview.id" page="nmcb-renewal-fee-review-status" />
    <bw-page-view v-if="registrarReview.id" :id="registrarReview.id" page="nmcb-registrar-renewal-review-status" />
  </v-container>
</template>
<script>
const querystring = require("querystring");
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      loading: false,
      renewDialog: false,
      renewalDate: '',
      dateMenu: false,
      renewalDateFormatted: '',
      regStatusCodes: [],
      regCompKey: 'registration'+0,
      licenseCompKey: 'license'+0,
      registration: {
        resource: {},
        statusCode: '',
        registrationStatus: {},
        registrationType: {}
      },
      license: {
        resource: {},
        number: '',
        start: '',
        end: '',
        status: ''
      },
      renewalApplication: {
        status: '',
        date: '',
        regNumbs: []
      },
      renewalFee: {
        id: ''
      },
      renewalFeeReview: {
        id: '',
        status: ''
      },
      registrarReview: {
        id: '',
        status: ''
      },
      practitionerId: '',
      applicationId: ''
    }
  },
  computed: {
    canRenewLicense() {
      if(
        this.hasTask('nmcb-license-profile') &&
        //allowing renewal of registrations with unknow type as well, change this after updating all registrations with reg type
        (this.registration.registrationType.code === 'permanent' || !this.registration.registrationType.code === '') &&
        this.registration.statusCode && (this.registration.statusCode == 'active' || this.registration.statusCode == 'lapsed')
      ) {
        return true
      }
      return false
    },
    canPrintCertificate() {
      if(
        this.hasTask('nmcb-print-certificates') &&
        this.registration.statusCode && this.registration.statusCode == 'active'
      ) {
        return true
      }
      return false
    },
    canAddRenewalFee() {
      if(!this.hasTask('nmcb-handling-fee-profile')) {
        return false
      }
      if(this.renewalApplication.status && this.renewalApplication.status.code === 'waitingfees') {
        return true
      }
      return false
    },
    canAddRenewalFeeReview() {
      if(!this.hasTask('nmcb-handling-fee-review-profile')) {
        return false
      }
      if(this.renewalFee.id && this.renewalFeeReview.status.code !== "approved") {
        return true
      }
      return false
    },
    canAddRegistrarReview() {
      if(!this.hasTask('nmcb-registration-review-profile')) {
        return false
      }
      if(this.renewalFeeReview.id && this.registrarReview.status.code !== "approved") {
        return true
      }
      return false
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    print_certificate(type) {
      this.overlay = true
      fetch("/site/nmcb/" + type + "/" + this.practitionerId).then((response) =>{
        response.blob().then((blob) => {
          fetch("/fhir/Practitioner/" + this.practitionerId).then((response) => {
            response.json().then((pract) => {
              this.overlay = false
              let fullname = ""
              let name = pract.name.find((nm) => {
                nm.use === "official"
              })
              if(!name && pract.name && pract.name.length > 0) {
                name = pract.name[0]
              }
              if(name) {
                let prefix = name.extension && name.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-prefix"
                })
                if(prefix) {
                  fullname += prefix.valueCoding.display
                }
                if(name.given) {
                  if(fullname) {
                    fullname += " "
                  }
                  fullname += name.given.join(" ")
                }
                if(name.family) {
                  if(fullname) {
                    fullname += " "
                  }
                  fullname += name.family
                }
              }
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = `${fullname}.pdf`;
              link.click();
            }).catch(() => {
              this.overlay = false
            })
          }).catch(() => {
              this.overlay = false
            })
        }).catch(() => {
          this.overlay = false
        })
      }).catch(() => {
        this.overlay = false
      })
    },
    getRenewalApplication() {
      let params = {
        '_profile': 'http://ihris.org/fhir/StructureDefinition/nmcb-license-renewal-application-profile',
        'practitioner': "Practitioner/" + this.practitionerId,
        '_sort': "-_id",
        '_count': 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(params)).then((response) => {
        response.json().then(async(renewal) => {
          if(renewal.entry && renewal.entry.length > 0) {
            this.applicationId = renewal.entry[0].resource.id
            this.renewalApplication.status = renewal.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-renewal-status"
            }).valueCoding
            this.renewalApplication.date = renewal.entry[0].resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/application-date"
            }).valueDate
            let regs = renewal.entry[0].resource.extension.filter((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/nmcb-registration-reference"
            })
            for(let reg of regs) {
              await fetch("/fhir/" + reg.valueReference.reference).then((resp) => {
                resp.json().then((registration) => {
                  let regNum = registration.extension.find((ext) => {
                    return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-number"
                  }).valueString
                  this.renewalApplication.regNumbs.push(regNum)
                })
              })
            }
            this.getRenewalFee()
          }
        })
      })
    },
    getRenewalFee() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-renewal-fee-profile',
        nmcblicenserenewal: 'Basic/' + this.applicationId,
        _sort: '_lastUpdated'
      }
      fetch("/fhir/Basic?" + querystring.stringify(query)).then(response => response.json().then((fee) => {
        if(fee.entry && fee.entry.length > 0) {
          this.renewalFee.id = fee.entry[0].resource.id
        }
        setTimeout(() => {
          this.getRenewalFeeReviewStatus()
        }, 500);
      }))
    },
    getRenewalFeeReviewStatus() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-renewal-fee-review-profile',
        nmcblicenserenewal: 'Basic/' + this.applicationId,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.renewalFeeReview.id = review.entry[0].resource.id
          this.renewalFeeReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
        }
        setTimeout(() => {
          this.getRegistrarReview()
        }, 500);
      }))
    },
    getRegistrarReview() {
      let query = {
        _profile: 'http://ihris.org/fhir/StructureDefinition/nmcb-registrar-renewal-review-profile',
        nmcblicenserenewal: 'Basic/' + this.applicationId,
        _sort: '-_id',
        _count: 1
      }
      fetch('/fhir/Basic?' + querystring.stringify(query)).then(response => response.json().then((review) => {
        if(review.entry && review.entry.length > 0) {
          this.registrarReview.id = review.entry[0].resource.id
          this.registrarReview.status = review.entry[0].resource.extension.find((ext) => {
            return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-status'
          }).valueCoding
        }
      }))
    },
    renew() {
      if(!this.hasTask('nmcb-license-profile') || !this.registration.registrationType.code === 'permanent') {
        return this.$router.push({path: '/'})
      }
      this.overlay = true;
      this.renewDialog = false
      let practitioner = this.registration.resource.extension.find((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
      }).valueReference.reference
      let licensenumber = this.registration.resource.extension.find((ext) => {
        return ext.url === 'http://ihris.org/fhir/StructureDefinition/registration-number'
      }).valueString
      let endDate
      if(this.renewalDate.split("-")[1] >= 9) {
        endDate = this.$moment(this.renewalDate).add(1, 'years').endOf('year').format("YYYY-MM-DD")
      } else {
        endDate = this.$moment(this.renewalDate).endOf('year').format("YYYY-MM-DD")
      }
      let license = {
        resourceType: "Basic",
        meta: {
          profile: ["http://ihris.org/fhir/StructureDefinition/nmcb-license-profile"]
        },
        extension: [{
          url: "http://ihris.org/fhir/StructureDefinition/start-date",
          valueDate: this.renewalDate
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/end-date",
          valueDate: endDate
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/license-number",
          valueString: licensenumber
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/nmcb-registration-reference",
          valueReference: {
            reference: "Basic/" + this.registration.resource.id
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference",
          valueReference: {
            reference: practitioner
          }
        }, {
          url: "http://ihris.org/fhir/StructureDefinition/ihris-related-group",
          extension: [{
            url: "practitioner",
            valueString: practitioner
          }]
        }]
      }
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify(license)
      }
      fetch("/fhir/Basic", opts).then((response) => {
        this.overlay = false;
        if(response.status === 201) {
          this.getLatestLicense()
          this.$store.commit("setMessage", {
            type: "success",
            text: "License Renewed",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to renew license."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to renew license."
        });
      })
    },
    getLatestLicense() {
      fetch("/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/nmcb-license-profile&_count=1&_sort=-_id&nmcbregistration=Basic/" + this.queries.id).then((response) => {
        response.json().then((license) => {
          if(license.entry && license.entry.length > 0) {
            this.license.resource = license.entry[0].resource
            this.licenseCompKey = 'license' + ++this.licenseCompKey.split('license')[1]
            this.license.number = this.license.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/license-number"
            }).valueString
            this.license.start = this.license.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/start-date"
            })?.valueDate
            this.license.end = this.license.resource.extension.find((ext) => {
              return ext.url === "http://ihris.org/fhir/StructureDefinition/end-date"
            })?.valueDate
            if(!this.license.start) {
              this.license.start = '-'
              this.license.end = '-'
              this.license.status = 'Waiting Registrar Approval'
            } else {
              if(this.$moment().isAfter(this.license.end)) {
                this.license.status = 'Expired'
              } else {
                this.license.status = 'Active'
              }
            }
          }
        })
      })
    },
    getRegistration() {
      fetch("/fhir/Basic/" + this.queries.id).then(response => response.json().then((registration) => {
        this.registration.resource = registration
        this.registration.registrationStatus = registration.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-status'
        }).valueCoding
        this.registration.registrationType = registration.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/nmcb-registration-type'
        }).valueCoding
        this.practitionerId = registration.extension.find((ext) => {
          return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
        }).valueReference.reference
        this.practitionerId = this.practitionerId.split("/")[1]
        this.registration.statusCode = this.registration.registrationStatus.code
        this.regCompKey = 'registration' + ++this.regCompKey.split('registration')[1]
        this.getLatestLicense()
        this.getRenewalApplication()
      }))
    },
    activateChangeStatus() {
      if(this.LicenseChangeStatusCodes.length === 0) {
        this.loadLicenseChangeStatusCodes()
      }
      this.changeLicenseStatusDialog = true
    },
    async loadRegStatusCodes() {
      this.loading = true
      this.regStatusCodes = await this.$fhirutils.expand("http://ihris.org/fhir/StructureDefinition/nmcb-registration-status-valueset")
      this.loading = false
    },
    hasTask(task) {
      return this.$store.state.user.obj.permissions && this.$store.state.user.obj.permissions.special && this.$store.state.user.obj.permissions.special.special && this.$store.state.user.obj.permissions.special.special.id && this.$store.state.user.obj.permissions.special.special.id[task]
    }
  },
  created() {
    this.getRegistration()
  },
  components: {
    "bw-page-view": () => import("@/site/view/bw-page-view")
  }
}
</script>